<template>
    <div>
        <div class="integral-cell-up">
            <span class="iconfont icon-huowu"></span>
            <span class="integral-name">{{goods.names}}</span>
            <span class="iconfont icon-toright"></span>
        </div>
        <div class="integral-cell-middle">
            <div class="right-float">
                <span class="price">￥{{goods.price}}</span>
            </div>
            <div v-show="goods.num!=1" class="num-div">
                <span class="num">{{goods.num}}</span>
            </div>
            <div class="cell-info">
                <div class="cell-info-left">
                    <div class="img-container">
                        <img v-lazy="goods.url" class="img" />
                    </div>
                </div>
                <div class="cell-info-right">
                    <p class="dec">{{goods.dec}}</p>
                    <p class="buy-time">购买时间：{{goods.buytm}}</p>
                    <span class="medal">正品</span>
                    <span class="medal">七天退货</span>
                </div>
            </div>
        </div>
        <div class="integral-cell-down">
            <div v-show="state=='1'" @click="backMoneyHandle" class="middle-item click-item">申请退款</div>
            <div v-show="state=='2'" @click="cencelHandle" class="middle-item click-item cacel">正在申请，是否取消</div>
            <div v-show="state=='3'" class="middle-item tip">已退款</div>
            <div v-show="state=='4'" class="middle-item tip">交易成功</div>
        </div>
    </div>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";
import { Button,Toast } from "mint-ui";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/toast/style.css";
Vue.component(Button.name, Button);
Vue.component(Toast.name, Toast);

export default {
  name: "smart-good-cell",
  data() {
    return {};
  },
  props: {
    goods: {
      type: Object,
      default: function() {
        return {
          url: "http://www.jubenxiong.com/aml/img/nx_1515661463680.png",
          names: "380美容套餐",
          dec: "",
          buytm: "2018-01-20",
          price: 333,
          num: 10,
          iscanreturn: false,
          orderstate: "0", //0未使用、1使用中、2已经使用过、3申请退款、4已经退款
          serialnum: "",
          id:""
        };
      }
    }
  },
  components: {},
  activated() {},
  created() {},
  computed: {
    ...mapState(["openid", "path","userid"]),
    state() {
      let orderstate = this.goods.orderstate;
      let iscanreturn = this.goods.iscanreturn;
      if (iscanreturn == true) {
        if (orderstate == "0") return "1";
        else if (orderstate == "3") return "2";
        else if (orderstate == "4") return "3";
      } else {
        if (orderstate == "0") return "4";
        else if (orderstate == "3") return "2";
        else if (orderstate == "4") return "3";
      }
    }
  },
  methods: {
    backMoneyHandle() {
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + `php/Route.php`, {
          type:'exitgoods',
          buyid:this.goods.id,
          serialnum:this.goods.serialnum
        })
        .then(response => {
          let irv = response.body.error.toString();
          if(irv=="ok"){
            this.goods.orderstate="3";
          }else if(irv=="-103"){
              Toast({message: "购买时间超过7天,申请退款失效!",iconClass: "iconfont icon-tishi1",duration: 2000});
              this.goods.iscanreturn=false;
          }
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response=>{
          //请求出错处理
          console.log(response);
          this.$store.dispatch("toggleLoading", false);
        });
    },
    cencelHandle() {
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + `php/Route.php`, {
          type:'exitexitgoods',
          buyid:this.goods.id,
          serialnum:this.goods.serialnum
        })
        .then(response => {
          let irv = response.body.error.toString();
          if(irv=="ok"){
            this.goods.orderstate="0";
            this.goods.iscanreturn=response.body.arr.iscanreturn;
          }else if(irv=="-103"){
            Toast({message: "购买时间超过7天,申请退款失效!",iconClass: "iconfont icon-tishi1",duration: 2000});
            this.goods.iscanreturn=false;
          }
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response=>{
          //请求出错处理
          console.log(response);
          this.$store.dispatch("toggleLoading", false);
        });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.integral-cell-up {
    width: 100%;
    height: 30px;
    line-height: 30px;
    color: #42495C;

    .icon-huowu {
        padding-left: 10px;
        font-size: 20px;
    }

    .integral-name {
        font-size: 15px;
        word-wrap: break-word;
        font-weight: 700;
    }

    .icon-toright {
        font-size: 13px;
        color: #acacac;
        font-weight: 700;
    }
}

.integral-cell-middle {
    margin: 0px 12px 0px 0px;
    position: relative;
    background-color: #efefef;
    border-radius: 0px 15px 0px 0px;

    .cell-info {
        font-size: 0px;
        display: flex;
        padding: 7px 0px 11px 0px;

        .cell-info-left {
            flex: 0 0 80px;
            text-align: center;

            .img-container {
                width: 60px;
                height: 60px;
                display: inline-block;

                .img {
                    position: relative;
                    width: 100%;
                    height: 100%;
                    // border-radius: 4px;
                }
            }
        }

        .cell-info-right {
            flex: 1;

            .dec {
                margin-top: 2px;
                font-size: 13px;
                margin-bottom: 3px;
                color: #42495C;
            }

            .buy-time {
                font-size: 12px;
                line-height: 14px;
                color: #acacac;
                margin-bottom: 2px;
            }

            .medal {
                display: inline-block;
                color: #FD8709;
                font-size: 12px;
                padding: 1px 4px;
                border-radius: 4px;
                border: 1px #FD8709 solid;
                margin-right: 2px;
            }
        }
    }

    .right-float {
        position: absolute;
        right: 12px;
        top: 50%;
        transform: translate(0, -50%);
        text-align: center;

        .price {
            color: #FD8709;
            font-size: 14px;
        }
    }

    .num-div {
        position: absolute;
        right: 0px;
        bottom: -5px;
        transform: translate(0, -50%);
        text-align: center;

        .num {
            display: inline-block;
            line-height: 14px;
            font-size: 14px;
            color: #fff;
            padding: 0px 3px 0px 6px;
            background-color: #7B808D;
            border-radius: 50% 0 0px 50%;
        }
    }
}

.integral-cell-down {
    width: 100%;
    height: 40px;
    text-align: right;
    position: relative;

    .middle-item {
        display: inline-block;
        right: 12px;
        top: 50%;
        transform: translate(0, -50%);
        position: absolute;
    }

    .click-item {
        color: #222;
        font-size: 12px;
        line-height: 12px;
        padding: 6px;
        border-radius: 15px;
        border: 1px #acacac solid;

        &.cacel {
            color: #999;
        }
    }

    .tip {
        color: #8a8a8a;
        font-size: 12px;
        font-weight: 700;
        padding-right: 5px;
    }
}
</style>



