<template>
    <div class="smart-goods-list">
        <div class="smart-content" v-infinite-scroll="requestData" infinite-scroll-disabled="cellLoading" infinite-scroll-distance="20" infinite-scroll-immediate-check="false">
            <li v-for="dataItem in goodsArr" :key="dataItem.id">
              <smart-good-cell class="smart-cell" :goods="dataItem">
              </smart-good-cell>
            </li>
        </div>
        <empty-bg :msg="emptyMsg" v-show="showEmpty"></empty-bg>
    </div>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";
import { formatDate } from "../../../common/js/date.js";
import EmptyBg from "../../popupBox/emptyBg.vue";
import smartGoodCell from "../../public/smartGoodCell.vue";
import { InfiniteScroll } from "mint-ui";
Vue.use(InfiniteScroll);

export default {
  name: "smart-goods-list",
  data() {
    return {
      goodsArr: [],
      showEmpty: false,
      emptyMsg: "您还未购买过该商品~",
      memoryScroll: 0,
      cellLoading: true,
      curPage: 0,
      pageNum: 10,
      existIdArr: []
    };
  },
  components: {
    EmptyBg,
    smartGoodCell
  },
  activated() {
    document.body.scrollTop = this.memoryScroll;
  },
  beforeRouteLeave(to, from, next) {
    this.memoryScroll = document.body.scrollTop;
    next();
  },
  created() {
    this.requestData(false);
  },
  methods: {
    requestData(infinite = true) {
      if (this.$route.name != "smartGoodsList") return;
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + `php/Route.php`, {
          type: "buylist",
          user: this.userid,
          goodstype: "3",
          page: this.curPage
        })
        .then(response => {
          this.$store.dispatch("toggleLoading", false);
          if (infinite && this.curPage == 0) return;
          if (infinite && this.cellLoading) return;
          this.cellLoading = true;
          let serverData = response.body;
          if (!serverData || serverData.length == 0) {
            //数据为空
            this.cellLoading = true;
            if (this.curPage == 0) {
              this.showEmpty = true;
            }
          } else {
            //有数据
            if (serverData.length % this.pageNum == 0) {
              //满一页数据
              this.curPage++;
              this.cellLoading = false;
            } else {
              //不满一页
              this.cellLoading = true;
            }
            let detailsArr = this.analysisData(serverData);
            for (let i = 0; i < detailsArr.length; i++) {
              let id = detailsArr[i].id;
              if (this.existIdArr.indexOf(id) >= 0) continue;
              this.goodsArr.push(detailsArr[i]);
              this.existIdArr.push(id);
            }
          }
        })
        .catch(response => {
          console.log(response);
          this.$store.dispatch("toggleLoading", false);
        });
    },
    analysisData(data) {
      let detailsArr = [];
      data.forEach((val, index, arr) => {
        let tempDate = new Date(val.buydate * 1000);
        let buyTime = formatDate(tempDate, "yyyy-MM-dd");
        detailsArr.push({
          id: val.buyid.toString(),
          names: val.setsname,
          url: this.path + val.icon,
          buytm: buyTime,
          price: val.price,
          dec: val.message,
          orderstate: val.orderstate.toString(), //orderstate 0未使用、1使用中、2已经使用过、3申请退款、4已经退款
          iscanreturn: val.iscanreturn, //iscanreturn为true表示在退款的时间之内
          serialnum: val.serialnum
        });
      });
      return detailsArr;
    }
  },
  computed: {
    ...mapState(["path", "userid"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.smart-goods-list {
  // height: 100%;
  .smart-content {
    width: 100%;
    padding-bottom: 1px;

    .smart-cell {
      width: 100%;
      background-color: #fff;
      margin-bottom: 0.07rem;
    }
  }

  .empty-bg {
    height: 75vh;
  }
}
</style>



