<template>
    <div class="empty-bg">
        <div class="emptybg-container">
            <img onclick="return false" class="img" src="../../assets/logo.png"/>
            <p class="tips">{{this.msg}}</p>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "empty-bg",
  props: {
    msg:""
  },
  components: {
  },
  created() {
    
  },
  methods: {

  },
  computed: {
    ...mapState(["path","userid"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.empty-bg{
    height 85%;
    width 100%;
    display: flex;
    align-items:center;
    text-align center;
    
    .emptybg-container{
        width 100%;
        .img{
            max-width:40%;
            height auto;
        }
        .tips{
            font-size:0.13rem;
            margin-top 0.1rem;
            color #424242;
            letter-spacing 0.005rem;
        }
    }
}
</style>



